@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.extend_btn {
  text-align: center;
  cursor: pointer;
  > img {
    width: 28px;
  }
}

.extend_btn_text {
  font-size: 12px;
  font-weight: 600;
  color: $color-a;
  cursor: pointer;
  > img {
    margin-left: 6px;
    height: 12px;
  }
}

@include mobile-only {
  .extend_btn {
    padding: 0 0 14px 0;
  }
}
