@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.farm_top_box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  @include mobile-only {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 0px;
  }
}

.farm_name {
  font-size: 20px;
  font-weight: 600;
  color: $color-text-default;
  @include mobile-only {
    font-size: 14px;
    margin-bottom: 0;
    margin-left: 6px;
  }
}

@include mobile-only {
  .farm_top_box_pump {
    margin-bottom: 0 !important;
  }
}
