@import '../../../assets/css/theme.scss';
@import '../../../assets/css/var.scss';
// 矿池index样式
.farm_wrap {
  background: $color-card-bg-active;
  padding: 20px 40px 20px;
  margin: 0 0 30px 0;
  border-radius: 0 0 10px 10px;
  :global {
    .panel-header-item {
      margin-right: 100px;
    }
  }
}

.stake_tab {
  span:nth-child(2) {
    width: 32px;
    height: 32px;
    display: inline-block;
    vertical-align: middle;
  }
}

.stake_active {
  span:nth-child(2) {
    width: 48px;
    height: 48px;
  }
}

.stake_tab1 {
  span:nth-child(2) {
    background: url(../../../assets/images/new-farm/v1_dark.svg) 100%/100%;
  }
  &.stake_active {
    span:nth-child(2) {
      background: url(../../../assets/images/new-farm/v1.svg) 100%/100% no-repeat;
    }
  }
}

.stake_tab2 {
  span:nth-child(2) {
    background: url(../../../assets/images/new-farm/v2_dark.svg) 100%/100% no-repeat;
  }
  &.stake_active {
    span:nth-child(2) {
      background: url(../../../assets/images/new-farm/v2.svg) 100%/100% no-repeat;
    }
  }
}

// 创世 / 第一阶段
.tab_content {
  display: flex;
  align-items: flex-start;
  margin-top: 20px;
  > div:nth-child(1) {
    width: 60%;
  }
  > div:nth-child(2) {
    width: 40%;
    margin-left: 40px;
  }
}

.tab_content_title {
  > span {
    font-size: 20px;
    font-weight: 600;
    color: $color-text-default;
  }
  > a {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-a;
  }
  img {
    vertical-align: baseline;
    margin-left: 4px;
    transform: rotate(90deg);
  }
}
.farm_tab_with_hot {
  position: relative;
  &:after {
    position: absolute;
    content: '';
    width: 51px;
    height: 24px;
    background: url('../../../assets/images/new-farm/hot.png');
    background-size: contain;
    right: -60px;
  }
}
@include mobile-only {
  .farm_wrap {
    padding: 12px 10px;
    :global {
      .panel-header-item {
        margin-right: 60px;
      }
    }
  }

  .stake_tab {
    span:nth-child(2) {
      width: 16px;
      height: 16px;
    }
  }
  .stake_active {
    span:nth-child(2) {
      width: 24px;
      height: 24px;
    }
  }
  .tab_content {
    flex-direction: column-reverse;
    div.tab_content_box {
      width: 100%;
      margin: 0;
      &:nth-child(2) {
        margin-bottom: 26px;
      }
    }
  }
  .farm_tab_with_hot {
    &:after {
      width: 34px;
      height: 16px;
      right: -44px;
    }
  }
}
.sun_title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include mobile-only {
    display: block;
  }
}
