@import './var.scss';
@import './theme.scss';

.apy-summary {
  > div {
    margin-bottom: 5px;
    font-family: 'PingFang HK', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-weight: 400;
    font-size: 10px;
    line-height: 20px;
    color: $white;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .tip-fix-apy {
    color: $color-a;
  }
  .tip-flex-apy {
    color: #97cdff;
  }
  .tip-gov-apy {
    color: #cfe8ff;
  }
}

.stakeTip1 {
  > span > span {
    color: #333333;
  }
}

.speed-to {
  > span > span {
    color: #ed635a;
  }
}
.lock-speed-tip {
  color: $color-a;
  text-decoration: underline;
  cursor: pointer;
}

.emer-text {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  .title {
    font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #666666;
  }
  .detail {
    font-family: 'Helvetica', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
      'WenQuanYi Micro Hei', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: #999;
    .claim-amount {
      font-weight: 700;
      color: #333;
    }
    .lock-rewards {
      font-weight: 700;
      color: #999;
    }
  }
}
.emer-warning {
  display: flex;
  font-family: 'PingFang SC', 'Lato', 'PingFang SC', 'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei',
    'WenQuanYi Micro Hei', sans-serif;
  align-items: flex-start;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 17px;
  color: #ed635a;
  margin-top: 30px;
  > img {
    margin-right: 5px;
    position: relative;
    top: 2px;
  }
}
.emer-btn {
  margin-top: 15px !important;
  margin-bottom: 6px;
}

.flex-apy {
  height: 63px;
}
