@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';
@import '../../../../assets/css/mixins';

.switch_wrap {
  @include darkThemeOnly {
    background: $color-text-default-1;
  }
  @include lightThemeOnly {
    background: #fbfbfb;
  }

  border-radius: 17px;
  border: 1px solid $color-text-default-3;

  > span {
    display: inline-block;
    padding: 6px 15px;
    font-size: 12px;
    font-weight: 400;
    color: $color-text-default;
    cursor: pointer;
  }

  .switch_active {
    background: $purple;
    color: $white;
    border-radius: 16px;
  }
}
