@import '../../../assets/css/theme.scss';
@import '../../../assets/css/var.scss';
// 第二阶段矿池样式
.farm_flex {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

.farm_card_flex {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  button {
    margin-top: 12px;
  }
}

.farm_margin {
  > div:nth-child(1) {
    margin: 0 10px 0 0;
  }
  > div:nth-child(3) {
    margin: 0 0 0 10px;
  }
}

.liquidity_card {
  background: $color-large-range-card-bg;
  border-radius: 10px;
  color: $color-text-default;
  padding: 0 20px 0 20px;
  margin-top: 20px;
}

// 矿池第一行
.liq_title {
  display: flex;
  justify-content: flex-start;
  padding: 20px 0;
  border-bottom: solid 1px $color-text-default-1;
  .liq_title_logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include mobile-only {
      margin-bottom: 20px;
    }
    img {
      height: 60px;
      white-space: nowrap;
      margin-right: 10px;
      @include mobile-only {
        height: 20px;
      }
    }
  }
}

.speed_info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .speed_info_tip {
    color: $color-text-default-5;
    margin-right: 8px;
    font-size: 12px;
  }
  .speed_info_tip_box {
    display: flex;
    @include mobile-only {
      display: block;
    }
  }
}

// 矿池第二行
.liq_info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.farm_status3 {
  &_top {
    display: flex;
    align-items: flex-start;
    img {
      width: 75px;
      height: 75px;
      margin-right: 12px;
    }
  }
  &_bottom {
    margin-top: 16px;
  }
  &_text1 {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default;
  }
  &_text2 {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-7;
    span {
      color: $color-text-default;
    }
  }
  &_text3 {
    font-size: 16px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: $color-text-default;
  }
}

.liq_card_260 {
  flex: none;
  width: 260px;
}

.w_100 {
  width: 100%;
}

.w_60 {
  width: 60%;
}

.w_40 {
  width: 40%;
}

.pc_show {
  display: block;
}

.mobile_show {
  display: none;
}

.column_between {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

@include mobile-only {
  .farm_flex {
    display: block;
  }

  .liquidity_card {
    padding: 0;
  }

  .farm_card_flex {
    flex-direction: column;
    > div {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
    }
    > div:nth-child(2) {
      margin-top: 16px;
    }
    button {
      margin-top: 0;
    }
  }

  .liq_card_260 {
    width: 100%;
  }

  .farm_margin {
    > div:nth-child(1) {
      margin: 0 0 10px 0;
    }
    > div:nth-child(3) {
      margin: 10px 0 0 0;
    }
  }

  .liq_title {
    flex-direction: column;
    border-bottom: none;
    padding: 10px 10px 0;
  }
  .speed_info {
    display: block;
    .speed_info_tip {
      font-size: 12px;
    }
  }
  .liq_info {
    flex-direction: column;
    border-bottom: solid 1px $color-text-default-1;
    padding: 0 10px 20px;
    margin-bottom: 12px;
  }

  .pc_show {
    display: none;
  }

  .mobile_show {
    display: block;
  }
}
