@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.info_box {
  display: flex;
  padding: 18px 0 22px;
}

.info_sigle_box {
  flex: 1;
}

.info_border {
  border-right: solid 1px $color-text-default-1;
}

.info_title {
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: $color-text-default-5;
  margin-bottom: 10px;
}

.info_value {
  font-size: 16px;
  font-family: Helvetica-Bold, Helvetica;
  font-weight: bold;
  color: $color-text-default;
}
.info_horizontal {
  display: flex;
  align-items: center;
  .info_title {
    margin-bottom: 0;
    margin-right: 10px;
  }
}
@include mobile-only {
  .info_box.module_important {
    display: block;
    width: 100%;
    padding: 0;
  }
  .info_sigle_box {
    display: flex;
    justify-content: space-between;
    text-align: left;
    margin-top: 16px;
    &:nth-child(1) {
      margin-top: 20px;
    }
  }
  .info_title {
    margin-bottom: 0;
  }
  .info_border {
    border-right: none;
  }
}
