@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.farm_link {
  color: $color-text-default-7;
  font-size: 12px;
  a {
    font-size: 12px;
    font-weight: 600;
    color: $color-a;
    margin-left: 10px;
    &:link,
    &:visited {
      color: $color-a;
    }
    &:hover {
      color: $color-a-hover;
    }
  }
  img {
    transform: rotate(90deg);
    vertical-align: baseline;
    margin-left: 4px;
  }
}
