@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.pool_flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .pool_search_input {
    width: 300px;
    height: 34px;
    font-size: 12px;
  }
}

.pool_btn_line {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.stake_switch_label {
  font-size: 14px;
  font-weight: 400;
  color: $color-text-default;
  font-family: PingFangSC-Regular, PingFang SC;
  margin-right: 10px;
}
.pool_setting {
  background: $color-primary;
  border-radius: 15px;
  padding: 6px 16px 6px 48px;
  position: relative;
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: $color-text-default;
  img {
    position: absolute;
    left: 0;
    top: -7px;
  }
  &:link,
  &:visited {
    color: $color-text-default;
  }
}

.pool_margin {
  margin-left: 10px;
}

.stake_label {
  font-size: 14px;
  font-family: PingFangSC, PingFang SC;
  font-weight: 400;
  color: $color-text-default;
  line-height: 20px;
  opacity: 0.5;
}
.stake_balance {
  font-size: 20px;
  font-family: PingFangSC, PingFang SC;
  font-weight: 600;
  color: $color-text-default;
  line-height: 28px;
}

@include mobile-only {
  .pool_flex {
    display: block;
    .pool_search_input {
      width: 100%;
    }
  }
  .pool_btn_line {
    &:nth-child(1) {
      margin-bottom: 12px;
    }
  }

  .pool_margin {
    margin-left: 0px;
    margin-bottom: 12px;
  }
}
