@import '../../../assets/css/mixins';

.common_switch[aria-checked='false'] {
  border: 1px solid #ffffff4d;
  box-sizing: content-box;
}
.common_switch[aria-checked='true'] {
  border: 1px solid transparent;
  box-sizing: content-box;
}
.common_switch:hover:not(.ant-switch-disabled)[aria-checked='false'] {
  @include lightThemeOnly {
    background: #ccc;
  }
}
