@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.farm_top_box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0;
  @include mobile-only {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 10px;
  }

  .pump_style {
    margin-left: 10px;
    display: flex;
    align-items: center;
    @include mobile-only {
      margin-left: 0;
      display: inline-block;
    }
    &_tip {
      height: 14px;
      margin-left: 6px;
      cursor: pointer;
    }
  }
}

.farm_subTitle {
  font-size: 14px;
  font-weight: 400;
  font-family: PingFangSC-Regular, PingFang SC;
  color: $color-text-default;
  margin-right: 16px;
  opacity: 0.5;
  @include mobile-only {
    font-size: 12px;
  }
}

.flex_icon {
  background: rgba(40, 202, 186, 0.1);
  border-radius: 12px;
  padding: 3px 8px;
  font-size: 12px;
  color: $color-icon-success;
}

.lock_icon {
  background: rgba(255, 172, 0, 0.1);
  border-radius: 12px;
  padding: 3px 8px;
  font-size: 12px;
  color: $color-icon-warning;
  margin-left: 8px;
}

.ended_icon {
  background: rgba(255, 74, 103, 0.1);
  border-radius: 12px;
  padding: 3px 8px;
  font-size: 12px;
  color: rgba(255, 74, 103, 1);
  margin-left: 8px;
}

.pump_tip_title {
  font-family: PingFangSC, PingFang SC;
  font-weight: 500;
  font-size: 12px;
  color: #ffffff;
  line-height: 17px;
  text-align: left;
  font-style: normal;
}
.pump_tip_content {
  font-family: PingFangSC, PingFang SC;
  font-weight: 400;
  font-size: 12px;
  color: #ffffff;
  line-height: 17px;
  text-align: left;
  font-style: normal;
}

@include mobile-only {
  .farm_top_box_pump {
    margin-bottom: 0 !important;
  }
}
